/* #region card */
.card {
  display: flex;
  width: 200px;
  flex-direction: column;
  padding-top: 32px;
  border: 1px solid #f3f3f3;
  align-items: center;
  box-sizing: border-box;
}

.card__img {
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  padding: 0 16px;
  margin-bottom: 4px;
}

.card__product-code {
  font-size: 10px;
  line-height: 14px;
  color: #616070;
  align-self: flex-start;
  padding-left: 16px;
  margin-bottom: 16px;
}

.card__feedback {
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  display: flex;
  gap: 55px;
  font-size: 12px;
  line-height: 18px;
  color: #616070;
}

.card__price-value {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #060b35;
}

.card__btn {
  margin: 16px;
  padding: 12px 0;
}

.card__link {
  background-color: #00acdc;
  color: white;
  font-size: 14px;
  font-weight: 700;
  border: 1px solid #00acdc;
  border-radius: 5px;
  text-decoration: none;
  text-transform: uppercase;
  padding: 12px 56px;
}

.card__link:hover {
  color: #00acdc;
  background-color: white;
}

.card__container {
  display: flex;
  margin-bottom: 24px;
  gap: 13px;
  align-items: flex-end;
}
/* #endregion */
